import onEvent from "./event-handler.js";
import { setBoard, setScore } from "./setters.js";

let piece = [
  {
    x: 4,
    y: 6,
    color: 1,
  },
  {
    x: 5,
    y: 6,
    color: 1,
  },
  {
    x: 6,
    y: 6,
    color: 1,
  },
  {
    x: 6,
    y: 7,
    color: 1,
  },
];

let board = emptyBoard();

function emptyBoard() {
  let board = [];
  for (let i = 0; i < 10; i++) {
    let temp_board = [];
    for (let j = 0; j < 24; j++) {
      temp_board.push(-1);
    }
    board.push(temp_board);
  }
  return board;
}

function display() {
  let rendered_board = [];
  for (let i = 0; i < 10; i++) {
    let temp_board = [];
    for (let j = 0; j < 24; j++) {
      temp_board.push(board[i][j]);
    }
    rendered_board.push(temp_board);
  }
  piece.forEach((element) => {
    rendered_board[element.x][element.y] = element.color;
  });
  setBoard(rendered_board);
  setScore(piece[0].y);
}

function gameloop() {
  for (let i = 0; i < 4; i++) {
    piece[i].y += 1;
  }
  display();
}

onEvent.gameloop(gameloop);
